
@use '../../styles/theme/variables' as *;
@use '../../styles/theme/mixins' as *;

.smartphone-shell {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 380px;
  height: 800px;
  background-color: #1a1a1a;
  border-radius: 40px;
  box-shadow:
    0 10px 40px rgba(0, 0, 0, 0.5),
    inset 0 0 5px rgba(0, 0, 0, 0.8);
  padding: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition:
    transform 0.4s ease-in-out,
    opacity 0.4s ease-in-out;
  z-index: 10000;
}

.status-bar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 30px;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 13px;
  color: #fff;
  z-index: 20;

  .time {
    font-weight: bold;
  }

  .battery-status {
    display: flex;
    align-items: center;
    gap: 6px;
    opacity: 0.8;
  }
}

.smartphone-screen {
  width: 100%;
  height: 100%;
  background-color: #050a15;
  border-radius: 25px;
  overflow: hidden;
  position: relative;
  @include custom-scrollbar;
  display: flex;
  flex-direction: column;
}

.home-indicator {
  // This is no longer used, but might be referenced in the main App.vue.
  // Clearing styles to avoid interference.
}

.app-fade-enter-active,
.app-fade-leave-active {
  transition: opacity 0.2s ease;
}

.app-fade-enter-from,
.app-fade-leave-to {
  opacity: 0;
}

.navigation-bar {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 48px;
  background-color: #1a1a1a;
  display: flex;
  justify-content: space-around;
  align-items: center;
  z-index: 100;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
}

.nav-btn {
  background: none;
  border: none;
  cursor: pointer;
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0.7;
  transition:
    opacity 0.2s ease,
    background-color 0.2s ease;

  &:hover {
    opacity: 1;
    background-color: rgba(255, 255, 255, 0.1);
  }
}

.menu-btn {
  position: relative;
  &::before,
  &::after {
    content: '';
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 20px;
    height: 2px;
    background-color: white;
    border-radius: 1px;
  }
  &::before {
    top: 16px;
    box-shadow:
      0 5px 0 white,
      0 10px 0 white;
  }
}

.home-btn {
  width: 26px;
  height: 26px;
  border: 2px solid white;
  border-radius: 50%;
}

.back-btn {
  width: 24px;
  height: 24px;

  &::before {
    content: '';
    display: block;
    width: 0;
    height: 0;
    border-left: 8px solid white;
    border-top: 6px solid transparent;
    border-bottom: 6px solid transparent;
  }
}
