
@use '../../../styles/theme/variables' as *;

.grid-layout {
  display: grid;
  grid-template-columns: 1fr;
  gap: $spacing-lg;
}

.detail-item {
  &.full-width {
    grid-column: 1 / -1;
  }
  .detail-key {
    color: $color-gold-pale;
    font-family: $font-family-title;
    font-size: $font-size-large;
    display: block;
    margin-bottom: $spacing-md;
  }
}

.memory-timeline {
  position: relative;
  padding-left: $spacing-lg;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 10px;
    bottom: 0;
    width: 2px;
    background-color: rgba($color-gold-liu, 0.15);
  }
}

.year-group h5 {
  cursor: pointer;
  font-family: $font-family-title;
  color: $color-gold-pale;
  margin: $spacing-lg 0 $spacing-md;
  user-select: none;
  position: relative;

  &::before {
    content: '';
    display: inline-block;
    margin-right: $spacing-sm;
    transition: transform 0.2s ease;
  }

  &.collapsed::before {
    transform: rotate(-90deg);
  }
}

.memory-list {
  display: flex;
  flex-direction: column;
  gap: $spacing-md;
  padding-left: $spacing-lg;
}

.memory-item {
  position: relative;
  background-color: rgba($color-black-void, 0.4);
  padding: $spacing-md;
  border-radius: $border-radius-sm;

  &::before {
    content: '';
    position: absolute;
    top: 18px;
    left: -22px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: $color-purple-mystery;
    border: 2px solid $color-black-void;
  }
}

.memory-event .tag {
  margin-right: $spacing-sm;
  font-size: $font-size-small;
  padding: 2px 6px;
}

.emotion-tag-anger {
  background-color: rgba(211, 47, 47, 0.2);
  color: #f44336;
}
.emotion-tag-sadness {
  background-color: rgba(33, 150, 243, 0.2);
  color: #2196f3;
}
.emotion-tag-fear {
  background-color: rgba(156, 39, 176, 0.2);
  color: #9c27b0;
}
.emotion-tag-humiliation {
  background-color: rgba(130, 119, 23, 0.2);
  color: #afad70;
}
.emotion-tag-satisfaction {
  background-color: rgba(76, 175, 80, 0.2);
  color: #4caf50;
}
.emotion-tag-joy {
  background-color: rgba(255, 193, 7, 0.2);
  color: #ffc107;
}

.memory-event,
.memory-influence {
  margin: 0;
  padding: 2px 0;
  color: $color-grey-stone;
  line-height: 1.6;

  strong {
    color: $color-purple-mystery;
    font-weight: bold;
  }
}

.placeholder {
  color: $color-grey-stone;
  font-style: italic;
}
