
/*  */
.destiny-card-panel-layout {
  height: 100%;
  width: 100%;
  background-color: #10141d;
  color: #fff;
}

.list-view-container {
  display: grid;
  grid-template-columns: 300px 1fr;
  height: 100%;
  gap: 1em;
  padding: 1em;

  @media (max-width: 600px) {
    grid-template-columns: 1fr;
    padding: 0;
    gap: 0;
  }
}

.card-list-section {
  display: flex;
  flex-direction: column;
  background-color: #1a1f2e;
  border-radius: 8px;
  overflow: hidden;

  @media (max-width: 600px) {
    border-radius: 0;
  }
}

.card-list {
  list-style: none;
  padding: 0;
  margin: 0;
  overflow-y: auto;
  flex-grow: 1;

  .empty-list {
    padding: 1em;
    text-align: center;
    color: #7f8c8d;
    font-style: italic;
  }

  li {
    padding: 1em;
    cursor: pointer;
    border-bottom: 1px solid #4a4a5e;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: background-color 0.2s ease;

    &:not(.empty-list):hover {
      background-color: #4a4a5e;
    }

    &.active {
      background-color: #f1c40f;
      color: #2c3e50;
      font-weight: bold;
    }
  }
}

.card-display-section-desktop {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border-radius: 8px;
  padding: 1em;

  .placeholder {
    color: #95a5a6;
    font-style: italic;
    font-size: 1.2em;
  }

  :deep(.destiny-card) {
    transform: scale(1);
    max-width: 400px;
    width: 100%;
  }

  @media (max-width: 600px) {
    display: none;
  }
}

.card-display-section-mobile {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  width: 100%;
  height: 100%;
  position: relative;

  .back-to-list-btn {
    position: absolute;
    top: 10px;
    left: 10px;
    background: rgba(0, 0, 0, 0.4);
    color: white;
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 20px;
    padding: 5px 12px;
    cursor: pointer;
    z-index: 10;
  }

  :deep(.destiny-card) {
    transform: scale(0.9);
    margin-top: 30px;
  }
}
