
.experience-bar {
  position: relative;
  height: 20px;
  background-color: #2a2f45;
  border-radius: 4px;
  overflow: hidden;
  margin: 10px 0;
  border: 1px solid rgba(212, 175, 55, 0.2);

  .progress-fill {
    height: 100%;
    background: linear-gradient(90deg, rgba(212, 175, 55, 0.5) 0%, rgba(212, 175, 55, 0.8) 100%);
    transition: width 0.5s ease;
  }

  .progress-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 12px;
    font-weight: bold;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7);
  }
}
.character-app-container {
  padding: 15px;
  height: 100%;
  overflow-y: auto;
  background-color: #10141d;
  color: #e0e0e0;
  position: relative;
}

.character-app {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.card {
  background-color: rgba(21, 26, 48, 0.4);
  border: 1px solid rgba(212, 175, 55, 0.15);
  border-radius: 12px;
  padding: 15px;
}

.character-header {
  display: flex;
  align-items: center;
}

.avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #151a30;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  font-weight: bold;
  color: #fafad2;
  margin-right: 15px;
  flex-shrink: 0;
  border: 2px solid rgba(212, 175, 55, 0.5);
}

.character-info {
  .character-name {
    font-family: 'Noto Serif SC', serif;
    font-size: 22px;
    margin: 0;
    color: #f0f0f0;
  }
  .character-identity {
    font-size: 13px;
    margin: 4px 0 0;
    color: #a0a0a0;
  }
}

.tabs {
  display: flex;
  background-color: rgba(21, 26, 48, 0.4);
  border-radius: 10px;
  padding: 4px;
  button {
    flex: 1;
    padding: 8px;
    border: none;
    background-color: transparent;
    color: #a0a0a0;
    cursor: pointer;
    transition:
      background-color 0.3s,
      color 0.3s;
    border-radius: 8px;
    &.active {
      background-color: rgba(212, 175, 55, 0.2);
      color: #fafad2;
      font-weight: bold;
    }
  }
}

.tab-content {
  .card {
    min-height: 300px;
  }
}

.status-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.status-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 12px;
  background-color: rgba(220, 20, 60, 0.15);
  border-left: 3px solid #dc143c;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
  font-size: 14px;

  &:hover {
    background-color: rgba(220, 20, 60, 0.25);
  }

  .status-duration {
    font-size: 12px;
    color: #a0a0a0;
  }
}

.attributes-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
}

.skills-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.skill-item {
  padding: 12px;
  background-color: rgba(30, 35, 55, 0.5);
  border-radius: 8px;
  border-left: 3px solid transparent;
  cursor: pointer;
  transition: all 0.3s;

  &:hover {
    background-color: rgba(45, 50, 75, 0.7);
    border-left-color: #d4af37;
  }

  .skill-header {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-bottom: 6px;
  }
  .skill-name {
    font-weight: bold;
    color: #f0f0f0;
    font-size: 15px;
  }
  .skill-level {
    font-size: 14px;
    font-weight: bold;
  }
  .skill-description {
    font-size: 13px;
    color: #a0a0a0;
    margin: 0;
    line-height: 1.5;
  }
}

.loading-pane,
.empty-state {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: 14px;
  color: #a0a0a0;
}

.empty-state {
  height: auto;
  padding: 20px;
}
.modal-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: #1c2135;
  border: 1px solid rgba(212, 175, 55, 0.3);
  border-radius: 8px;
  padding: 20px;
  width: 90%;
  max-width: 400px;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(212, 175, 55, 0.2);
  padding-bottom: 10px;
  margin-bottom: 15px;

  h3 {
    margin: 0;
    font-size: 18px;
    color: #fafad2;
  }

  .close-button {
    background: none;
    border: none;
    color: #a0a0a0;
    font-size: 24px;
    cursor: pointer;
  }
}

.modal-body {
  p {
    margin: 0 0 10px;
    font-size: 14px;
    line-height: 1.6;
    color: #c0c0c0;

    strong {
      color: #fafad2;
    }
  }
}
