
@use '../../styles/theme/variables.scss' as *;

.home-screen {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: $spacing-lg;
  padding-top: 40px; // Space for notch
}

.wallpaper-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-size: cover;
  background-position: center;
  transition: background-image 0.5s ease-in-out;

  // Add a subtle vignette to make text more readable
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.4) 100%);
  }
}

.content-overlay {
  position: relative;
  z-index: 2;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.header-info {
  text-align: center;
  color: $color-white-moon;
  padding: $spacing-lg 0;
  flex-shrink: 0;

  .date {
    font-size: $font-size-base;
    opacity: 0.7;
  }
  .time {
    font-size: $font-size-h1 * 1.2;
    font-weight: 300;
    margin-top: $spacing-xs;
    color: $color-white-moon;
  }
}

.app-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  flex-grow: 1;
  gap: $spacing-lg;
  padding: $spacing-xl 0;
}
