
.character-panel {
  background: #f5f1e9;
  padding: 15px;
  overflow-y: auto;
  box-shadow: inset 0 0 10px rgba(58, 63, 75, 0.03);
  border-right: 1px solid #eae6dd;
}

.panel-section {
  margin-bottom: 20px;
  padding: 15px;
  background: white;
  border-radius: 8px;
  border: 1px solid #eae6dd;
  box-shadow: 0 2px 8px rgba(58, 63, 75, 0.05);
  transition: box-shadow 0.3s ease;

  &:hover {
    box-shadow: 0 4px 12px rgba(58, 63, 75, 0.08);
  }
}

.section-title {
  font-size: 14px;
  font-weight: 600;
  color: #3a3f4b;
  margin-bottom: 12px;
  text-align: left;
  border-bottom: 2px solid #4169e1;
  padding-bottom: 8px;
  display: inline-block;
}

.attributes-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.attribute-item {
  display: flex;
  justify-content: space-between;
  font-size: 13px;
}

.attribute-name {
  color: #6c757d;
}

.attribute-value {
  color: #3a3f4b;
  font-weight: 500;
}
