
@use '../../styles/theme/variables' as *;

.npc-directory-panel-mobile {
  height: 100%;
  color: $color-white-moon;
  display: flex;
  flex-direction: column;
}

.npc-list-view,
.npc-detail-view {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.search-input {
  width: 100%;
  padding: $spacing-md;
  margin-bottom: $spacing-md;
  background-color: rgba($color-black-void, 0.8);
  border: 1px solid $color-gold-liu;
  color: $color-white-moon;
  border-radius: $border-radius-md;
  font-size: $font-size-base;
}

.npc-list {
  list-style: none;
  padding: 0;
  margin: 0;
  flex-grow: 1;
  overflow-y: auto;

  li {
    padding: $spacing-md;
    cursor: pointer;
    border-bottom: 1px solid rgba($color-gold-liu, 0.2);
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: background-color 0.2s ease;

    &:hover {
      background-color: rgba($color-gold-pale, 0.1);
    }
  }
}

.npc-info {
  display: flex;
  flex-direction: column;
}

.npc-name {
  font-weight: bold;
  font-size: $font-size-large;
}

.npc-location {
  font-size: $font-size-small;
  color: $color-grey-stone;
  margin-top: 4px;
}

.detail-header {
  padding: $spacing-sm 0;
  margin-bottom: $spacing-md;
}

.back-button {
  background: none;
  border: none;
  color: $color-gold-pale;
  font-size: $font-size-base;
  cursor: pointer;

  i {
    margin-right: $spacing-sm;
  }
}

/* Slide-fade transition for view switching */
.slide-fade-enter-active {
  transition: all 0.3s ease-out;
}

.slide-fade-leave-active {
  transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}

.slide-fade-enter-from,
.slide-fade-leave-to {
  transform: translateX(10px);
  opacity: 0;
}
