
@use '../../styles/theme/variables.scss' as *;
@use '../../styles/theme/mixins' as *;

.timeline-panel {
  color: $color-white-moon;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;

  .calendar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: $spacing-lg;

    .panel-title {
      font-family: $font-family-title;
      font-size: $font-size-h2;
      color: $color-gold-pale;
      margin: 0;
    }

    button {
      background: transparent;
      border: 1px solid $color-gold-pale;
      color: $color-gold-pale;
      cursor: pointer;
    }
  }

  .calendar-grid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 2px;

    .day-header {
      text-align: center;
      font-weight: bold;
      color: $color-grey-stone;
      padding-bottom: $spacing-sm;
    }

    .day-cell {
      border: 1px solid rgba($color-gold-liu, 0.2);
      padding: $spacing-xs;
      display: flex;
      flex-direction: column;
      min-height: 50px; /* Reduced height */
      cursor: pointer;
      transition: background-color 0.2s;

      &:hover {
        background-color: rgba($color-gold-pale, 0.05);
      }

      &.selected {
        background-color: rgba($color-gold-pale, 0.1);
        border-color: $color-gold-pale;
      }

      &.other-month {
        .day-number {
          color: $color-grey-stone;
          opacity: 0.5;
        }
      }
    }

    .day-number {
      font-weight: bold;
      margin-bottom: $spacing-sm;
    }

    .events-container {
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 4px;
    }

    .event-marker {
      background-color: $color-gold-liu;
      border-radius: 50%;
      width: 8px;
      height: 8px;
    }
    .festival-marker {
      background-color: $color-cyan-tian;
    }
  }

  .details-drawer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: auto;
    top: calc(100% - 40px); /* Initially just the handle is visible */
    background-color: #101828;
    border-top: 1px solid $color-gold-liu;
    transition: top 0.4s ease-in-out;
    display: flex;
    flex-direction: column;

    &.is-open {
      top: 120px; /* Adjust this value to align below the weekday header */
    }
  }

  .drawer-handle {
    flex-shrink: 0;
    height: 40px;
    cursor: pointer;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 40px;
      height: 4px;
      background-color: rgba($color-gold-liu, 0.7);
      border-radius: 2px;
    }
  }

  .details-content {
    flex-grow: 1;
    overflow-y: auto;
    padding: 0 $spacing-md $spacing-md;
    @include custom-scrollbar;
  }

  .no-selection-message {
    text-align: center;
    color: $color-grey-stone;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .day-details {
    .detail-section {
      margin-bottom: $spacing-lg;
      h4 {
        font-family: $font-family-title;
        font-size: $font-size-h3;
        color: $color-gold-pale;
        margin-bottom: $spacing-md;
      }
      p {
        color: $color-white-moon;
        line-height: 1.6;
      }
      .stage-summary {
        font-style: italic;
        color: $color-grey-stone;
        margin-bottom: $spacing-md;
      }
    }
  }
}
