
@use './styles/theme/variables' as *;
@use './styles/theme/mixins' as *;

.guixu-root-container {
  display: grid;
  grid-template-rows: auto 1fr;
  width: 100%;
  max-width: 1400px;
  margin: auto;
  overflow: hidden;
  aspect-ratio: 16 / 12;
  min-height: 900px;
  max-height: 90vh;
  border: 1px solid rgba($color-gold-liu, 0.2);
  border-radius: 12px;
  box-shadow: 0 8px 35px rgba($color-black-void, 0.5);
  background: $color-black-void;
  position: relative;
}

.web-fullscreen {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  width: 100vw !important;
  height: 100vh !important;
  aspect-ratio: auto !important;
  overflow: visible !important;
  border-radius: 0 !important;
  border: none !important;
  z-index: 1;
}

.main-layout {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  overflow: hidden;

  & > * {
    grid-column: 1 / -1;
    grid-row: 1 / -1;
  }
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba($color-black-void, 0.7);
  backdrop-filter: blur(5px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 90000;
}

.modal-content {
  @include frosted-glass(rgba($color-indigo-deep, 0.8), 12px);
  border: 1px solid rgba($color-gold-liu, 0.4);
  border-radius: $border-radius-md;
  padding: $spacing-lg;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  min-width: 320px;
  max-width: 500px;

  .confirmation-title {
    color: $color-gold-pale;
    font-family: $font-family-title;
    font-size: $font-size-h2;
    text-align: center;
    margin-bottom: $spacing-md;
  }

  .confirmation-message {
    color: $color-white-moon;
    font-size: $font-size-base;
    line-height: $line-height-base;
    margin-bottom: $spacing-xl;
    text-align: center;
  }

  .confirmation-buttons {
    display: flex;
    justify-content: flex-end;
    gap: $spacing-md;
  }
}

.detail-modal {
  width: 800px;
  max-width: 90vw;
  height: 600px;
  max-height: 80vh;
  display: flex;
  flex-direction: column;

  .modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: $spacing-md;
    margin-bottom: $spacing-lg;
    border-bottom: 1px solid rgba($color-gold-liu, 0.3);

    .modal-title {
      color: $color-gold-pale;
      font-family: $font-family-title;
      font-size: $font-size-h2;
    }

    .close-button {
      background: none;
      border: none;
      color: $color-grey-stone;
      font-size: 24px;
      cursor: pointer;
      &:hover {
        color: $color-white-moon;
      }
    }
  }

  .modal-body {
    overflow-y: auto;
    padding-right: $spacing-sm;
    color: $color-white-moon;
    flex-grow: 1;
    min-height: 0;
  }
}

.small-detail-modal {
  max-width: 400px !important;
  height: auto !important;
  max-height: 80vh;
}

.card-detail-modal {
  width: auto;
  max-width: 400px; /* Or a suitable width for the card */
  height: auto;
  max-height: 90vh;
  padding: 0;
  background: transparent;
  border: none;
  box-shadow: none;

  :deep(.modal-header) {
    display: none; /* Hide modal header for cards */
  }

  :deep(.modal-body) {
    padding: 0;
    overflow: visible;
  }
}

.input-modal-overlay {
  align-items: flex-end;
  padding-bottom: 5vh;
  background-color: transparent;
  backdrop-filter: none;
  pointer-events: none;
}

.input-modal-content {
  pointer-events: auto;
  @include frosted-glass(rgba($color-indigo-deep, 0.8), 12px);
  border: 1px solid rgba($color-gold-liu, 0.4);
  border-radius: $border-radius-md;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  padding: 0;
  width: 70%;
  max-width: 900px;
}

.input-area-container {
  position: relative;
}

.input-area-with-buttons {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
}

.action-panel {
  position: absolute;
  bottom: calc(100% + #{$spacing-md});
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
}

.command-queue-popup,
:deep(.input-history-popup) {
  position: absolute;
  bottom: calc(100% + #{$spacing-md});
  left: 50%;
  transform: translateX(-50%);
  width: 80%;
  max-width: 600px;
  z-index: 20;
  border-radius: $border-radius-md;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  padding: $spacing-md;
  display: flex;
  flex-direction: column;
  gap: $spacing-md;

  .popup-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: $spacing-sm;
    border-bottom: 1px solid rgba($color-gold-liu, 0.2);

    h3 {
      color: $color-gold-pale;
      font-family: $font-family-title;
      font-size: $font-size-h3;
      margin: 0;
    }

    .close-popup-button {
      background: none;
      border: none;
      color: $color-grey-stone;
      font-size: 22px;
      cursor: pointer;
      line-height: 1;

      &:hover {
        color: $color-white-moon;
      }
    }
  }

  .popup-body {
    .command-list {
      max-height: 200px;
      overflow-y: auto;
      display: flex;
      flex-direction: column;
      gap: $spacing-sm;
      padding-right: $spacing-sm;
    }

    .command-item {
      background-color: rgba($color-black-void, 0.3);
      padding: $spacing-sm $spacing-md;
      border-radius: $border-radius-sm;
      font-family: $font-family-main;
      color: $color-white-moon;
      word-break: break-all;
    }

    .empty-queue {
      text-align: center;
      color: $color-grey-stone;
      padding: $spacing-lg 0;
    }
  }

  .popup-footer {
    display: flex;
    justify-content: flex-end;
    padding-top: $spacing-sm;
    border-top: 1px solid rgba($color-gold-liu, 0.2);
  }
}

:global(html.web-fullscreen-active),
:global(body.web-fullscreen-active) {
  overflow: hidden !important;
}

:global(body.web-fullscreen-active #chat) {
  padding: 0 !important;
  margin: 0 !important;
  height: 100vh !important;
  max-height: 100vh !important;
}

:global(body.web-fullscreen-active #chat > div) {
  padding: 0 !important;
  margin: 0 !important;
}

:global(body.web-fullscreen-active .mes_block) {
  padding: 0 !important;
  margin: 0 !important;
}

:global(body.web-fullscreen-active .mes_text) {
  padding: 0 !important;
  margin: 0 !important;
}

.floating-cards-container {
  position: absolute;
  top: 60px; /* Adjust based on TopStatusBar height */
  right: 20px;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: -80px; // Negative gap to stack cards
}

.smartphone-toggle-btn {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: $color-gold-liu;
  color: $color-black-void;
  border: none;
  font-size: 24px;
  cursor: pointer;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 80000;
  transition:
    background-color 0.3s ease,
    transform 0.3s ease;

  &:hover {
    background-color: lighten($color-gold-liu, 10%);
    transform: scale(1.1);
  }
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}

.fade-enter-active .smartphone-shell,
.fade-leave-active .smartphone-shell {
  transition: transform 0.4s ease-out;
}

.fade-enter-from .smartphone-shell,
.fade-leave-to .smartphone-shell {
  transform: translateY(20px) scale(0.95);
}
