
.world-state-panel {
  padding: 15px;
}

.panel-section {
  margin-bottom: 20px;
  padding: 15px;
  background: white;
  border-radius: 8px;
  border: 1px solid #eae6dd;
}

.section-title {
  font-size: 14px;
  font-weight: 600;
  color: #3a3f4b;
  margin-bottom: 12px;
}

.state-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
