
@use '../../styles/theme/variables' as *;

.realm-detail-card {
  padding: $spacing-md;
  min-width: 400px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: $spacing-lg;

  .info-item {
    display: flex;
    flex-direction: column;
    gap: $spacing-xs;

    strong {
      font-family: $font-family-main;
      color: $color-gold-pale;
      font-size: 12px;
      text-transform: uppercase;
      letter-spacing: 1px;
      opacity: 0.7;
    }

    span,
    ul {
      font-size: 14px;
      color: $color-white-moon;
      line-height: 1.6;
    }

    ul {
      padding-left: 20px;
    }

    &.full-width {
      grid-column: 1 / -1;
    }
  }
}
