
@use '../../styles/theme/variables' as *;

.progress-bar-container {
  display: flex;
  align-items: center;
  gap: $spacing-md;
  width: 100%;
}

.label {
  color: $color-grey-stone;
  font-size: $font-size-small;
  flex-basis: 60px;
  flex-shrink: 0;
  white-space: nowrap;
}

.progress-track {
  position: relative;
  flex-grow: 1;
  height: 8px;
  background-color: rgba($color-black-void, 0.5);
  border-radius: 4px;
  overflow: hidden;
}

.center-tick {
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 1px;
  background-color: rgba($color-grey-stone, 0.5);
}

.progress-fill {
  position: absolute;
  top: 0;
  bottom: 0;
  height: 100%;
  border-radius: 4px;
  transition:
    width 0.5s ease,
    left 0.5s ease,
    right 0.5s ease;
}

.value {
  color: $color-white-moon;
  font-size: $font-size-small;
  font-weight: bold;
  flex-basis: 40px;
  flex-shrink: 0;
  text-align: right;
}
