
.debug-panel {
  padding: 16px;
  background-color: #1a1a2e;
  color: #e0e0e0;
  height: 100%;
  overflow: auto;
  font-family: 'Courier New', Courier, monospace;
}

h2,
h3 {
  color: #fca311;
  border-bottom: 1px solid #4a4a5e;
  padding-bottom: 8px;
}

.section {
  margin-top: 16px;
  background-color: #2a2a3e;
  border: 1px solid #4a4a5e;
  border-radius: 4px;
  padding: 12px;
}

pre {
  white-space: pre-wrap;
  word-break: break-all;
  background-color: #0f0f1e;
  padding: 8px;
  border-radius: 4px;
  max-height: 300px;
  overflow-y: auto;
}

button {
  background-color: #fca311;
  color: #1a1a2e;
  border: none;
  padding: 8px 12px;
  cursor: pointer;
  border-radius: 4px;
  font-weight: bold;
  margin-bottom: 16px;
  &:hover {
    background-color: #e0930a;
  }
}

.status-success {
  color: #2ecc71;
  font-weight: bold;
}

.story-stats {
  font-size: 0.8em;
  color: #999;
  margin-left: 1em;
  margin-top: -8px;
  margin-bottom: 8px;
}

.status-error {
  color: #e74c3c;
  font-weight: bold;
}
