
@use '../../styles/theme/variables' as *;
@use '../../styles/theme/mixins' as *;

.memory-panel-container {
  padding: 0;
  background-color: transparent;
  color: $color-white-moon;
  overflow-y: auto;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.panel-content {
  display: flex;
  flex-direction: column;
  gap: $spacing-lg;
  height: 100%;
}

.panel-title {
  font-family: $font-family-title;
  color: $color-gold-pale;
  text-align: center;
  margin-bottom: $spacing-lg;
  flex-shrink: 0;
}

.section {
  margin-bottom: $spacing-lg;
  padding-bottom: $spacing-lg;
  border-bottom: 1px solid rgba($color-gold-liu, 0.2);

  &:last-child {
    border-bottom: none;
    margin-bottom: 0;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }

  h4 {
    color: $color-gold-liu;
    margin-bottom: $spacing-md;
  }

  .description {
    font-size: $font-size-small;
    color: $color-grey-stone;
    line-height: 1.6;
    margin-top: $spacing-sm;
  }
}

.setting-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: $spacing-md;

  label {
    color: $color-grey-stone;
  }

  input[type='number'] {
    width: 80px;
    text-align: right;
  }
}

button {
  margin-right: $spacing-md;
}

.tabs {
  display: flex;
  margin-bottom: $spacing-md;
  border-bottom: 1px solid rgba($color-gold-liu, 0.3);
  flex-shrink: 0;

  button {
    background: none;
    border: none;
    color: $color-grey-stone;
    padding: $spacing-sm $spacing-md;
    cursor: pointer;
    transition: all 0.2s ease;
    border-bottom: 2px solid transparent;

    &.active {
      color: $color-gold-pale;
      border-bottom-color: $color-gold-pale;
    }
  }
}

.memory-content {
  flex-grow: 1;

  textarea {
    width: 100%;
    height: 100%;
    min-height: 200px;
    resize: none;
    background-color: rgba($color-black-void, 0.5);
    color: $color-white-moon;
    border: 1px solid rgba($color-gold-liu, 0.2);
    border-radius: $border-radius-sm;
  }
}
