
.world-detail-panel-content {
  color: #e0e0e0;
  padding: 16px;
  height: 100%;
  overflow: hidden;
  background: transparent;
  font-family: 'Noto Sans SC', sans-serif;
}

.scroll-container {
  height: 100%;
  overflow-y: auto;
  padding-right: 12px;
}

.world-section {
  margin-bottom: 24px;
}
.world-section:last-child {
  margin-bottom: 0;
}

.epoch-header {
  text-align: center;
  margin-bottom: 16px;
}
.epoch-header .section-title {
  margin-bottom: 8px;
}
.epoch-header .epoch-overview {
  font-size: 0.9em;
  color: #a0a0a0;
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.5;
}

.section-title {
  font-family: 'Noto Serif SC', serif;
  font-size: 1.5em;
  color: #f0e6d2;
  text-align: center;
  margin-bottom: 16px;
  text-shadow: 0 0 10px rgba(184, 134, 11, 0.5);
}

.rules-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 12px;
}

.rule-item {
  background-color: rgba(40, 42, 58, 0.7);
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  padding: 12px;
  border-radius: 8px;
  text-align: center;
  border: 1px solid rgba(17, 233, 225, 0.2);
}

.rule-item .rule-name {
  font-size: 0.8em;
  color: #a0a0a0;
  margin-bottom: 8px;
}

.rule-item .rule-value {
  font-size: 1.25em;
  font-weight: bold;
  color: #82d8d8;
}

.system-overview {
  font-size: 1em;
  line-height: 1.6;
  text-align: justify;
  margin-bottom: 16px;
  padding: 12px;
  background-color: rgba(10, 10, 15, 0.3);
  border-radius: 4px;
}

.subsection-title {
  font-family: 'Noto Serif SC', serif;
  font-size: 1.25em;
  color: #b8860b;
  margin-bottom: 12px;
  padding-bottom: 8px;
  border-bottom: 1px solid rgba(184, 134, 11, 0.3);
}

.tags-container {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.tag {
  background-color: rgba(17, 233, 225, 0.2);
  color: #82d8d8;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 0.8em;
}

.ladder-container {
  background: linear-gradient(to top, rgba(10, 10, 15, 0.5) 0%, transparent 100%);
  padding: 12px;
  border-radius: 8px;
}

.-ladder {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column-reverse;
  gap: 8px;
}

.ladder-step {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px;
  border-radius: 4px;
  background-color: rgba(28, 32, 53, 0.5);
  border-left: 3px solid #b8860b;
  cursor: help;
  transition: all 0.2s ease;
}

.ladder-step:hover {
  background-color: rgba(62, 71, 112, 0.5);
  transform: translateX(5px);
}

.ladder-step .level-name {
  font-size: 1.1em;
  font-weight: bold;
}

.ladder-step .level-energy {
  font-size: 0.8em;
  color: #a0a0a0;
  background-color: rgba(10, 10, 15, 0.5);
  padding: 4px 8px;
  border-radius: 4px;
}

.epoch-selection {
  .tabs-container {
    display: flex;
    justify-content: center;
    gap: 12px;
    margin-bottom: 16px;
  }
  .epoch-tab {
    background-color: rgba(40, 42, 58, 0.7);
    -webkit-backdrop-filter: blur(8px);
    backdrop-filter: blur(8px);
    border: 1px solid rgba(17, 233, 225, 0.2);
    color: #a0a0a0;
    padding: 8px 12px;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;
  }

  .epoch-tab.active {
    color: #82d8d8;
    border-color: #11e9e1;
    background-color: rgba(17, 233, 225, 0.2);
  }

  .epoch-tab:hover:not(.active) {
    background-color: rgba(62, 71, 112, 0.3);
  }
}

.loading-state {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-family: 'Noto Serif SC', serif;
  color: #f0e6d2;
}
.summary-content {
  padding: 16px;
  line-height: 1.7;
  white-space: normal; /*  */
}
:deep(.summary-content p) {
  margin-bottom: 1.5em; /*  */
}
:deep(.summary-content p:last-child) {
  margin-bottom: 0;
}
