
@use '../../styles/theme/variables' as *;

.game-text-container {
  font-size: 15px;
  line-height: 1.8;
  color: #000000;
  white-space: pre-wrap;

  .text-dialogue {
    color: $color-gold-liu;
  }

  .text-psychology {
    color: $color-purple-mystery;
    font-style: italic;
  }

  .text-scenery {
    color: $color-cyan-tian;
  }

  .text-system-highlight {
    color: $color-gold-liu;
    font-weight: bold;
  }

  :deep(.npc-name) {
    font-family: $font-family-title;
    cursor: pointer;
    text-decoration: underline;
    text-decoration-color: rgba(255, 255, 255, 0.5);
    transition: all 0.2s ease;
    text-shadow:
      -1px -1px 0 #000,
      1px -1px 0 #000,
      -1px 1px 0 #000,
      1px 1px 0 #000;

    &:hover {
      background-color: rgba(255, 255, 255, 0.15);
      text-decoration-color: #fff;
    }

    &.npc-hostile-intense {
      color: #ff4d4d;
    }
    &.npc-hostile {
      color: #ff8c66;
    }
    &.npc-unfriendly {
      color: #ffd700;
    }
    &.npc-neutral {
      color: #ffffff;
    }
    &.npc-friendly {
      color: #90ee90;
    }
    &.npc-devoted {
      color: #87cefa;
    }
    &.npc-fanatical {
      color: #ff69b4;
    }
    &.npc-unknown {
      color: #9e9e9e;
    }
  }

  :deep(.location-name) {
    color: #80b9ea; /* A light cyan color */
    cursor: pointer;
    text-shadow:
      -1px -1px 0 #000,
      1px -1px 0 #000,
      -1px 1px 0 #000,
      1px 1px 0 #000;
    text-decoration: underline;
    text-decoration-color: rgba(128, 222, 234, 0.5);

    &:hover {
      background-color: rgba(128, 151, 234, 0.15);
      text-decoration-color: #80b5ea;
    }
  }

  :deep(.database-item-name) {
    color: #f8cbad; /* A light orange color for items */
    cursor: pointer;
    text-shadow:
      -1px -1px 0 #000,
      1px -1px 0 #000,
      -1px 1px 0 #000,
      1px 1px 0 #000;
    text-decoration: underline;
    text-decoration-color: rgba(248, 203, 173, 0.5);

    &:hover {
      background-color: rgba(248, 203, 173, 0.15);
      text-decoration-color: #f8cbad;
    }
  }
}
