
.location-node {
  margin-left: 20px;
  list-style: none;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 15px;
    left: -12px;
    width: 10px;
    height: 1px;
    background-color: #444;
  }
}

.node-content {
  cursor: pointer;
  padding: 5px 0;
  display: flex;
  align-items: center;

  .arrow {
    display: inline-block;
    transition: transform 0.2s;
    margin-right: 8px;
    font-size: 10px;
  }
}

.expanded > .node-content .arrow {
  transform: rotate(90deg);
}

.node-details {
  padding-left: 15px;
  border-left: 1px solid #444;
  margin-left: -7px;

  .description {
    font-size: 13px;
    color: #a0a0a0;
    margin: 5px 0;
    padding-left: 5px;
  }

  .characters {
    font-size: 13px;
    margin: 10px 0;
    padding-left: 5px;
    .character-tag {
      background-color: rgba(58, 143, 157, 0.2);
      color: #3a8f9d;
      padding: 2px 6px;
      border-radius: 5px;
      margin-left: 8px;
    }
  }
}

.children-list {
  padding: 0;
  margin: 10px 0 0 0;
  list-style: none;
}
