
.inventory-panel-container {
  padding: 15px;
  height: 100%;
  overflow: hidden;
  background-color: #10141d;
  color: #e0e0e0;

  @media (max-width: 600px) {
    padding: 0;
  }
}

.inventory-layout {
  height: 100%;
  width: 100%;
  position: relative;
}

.list-view-container {
  display: grid;
  grid-template-rows: auto 1fr;
  gap: 15px;
  height: 100%;
  padding: 15px;

  @media (max-width: 600px) {
    padding: 0;
  }
}

.category-header {
  flex-shrink: 0;
  background-color: rgba(21, 26, 48, 0.4);
  border-radius: 10px;
  padding: 5px;
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    gap: 5px;
  }
  li {
    padding: 8px 12px;
    cursor: pointer;
    border-radius: 8px;
    transition: background-color 0.3s ease;
    color: #a0a0a0;
    font-size: 14px;
    &:hover {
      background-color: rgba(58, 143, 157, 0.1);
    }
    &.active {
      background-color: rgba(58, 143, 157, 0.2);
      color: #3a8f9d;
      font-weight: bold;
    }
  }
}

.main-content {
  display: grid;
  grid-template-columns: 1fr;
  gap: 15px;
  min-height: 0;
}

.item-grid-container {
  overflow-y: auto;
  padding-right: 5px;
}

.item-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 10px;
}

.item-cell {
  aspect-ratio: 1;
  border: 1px solid var(--tier-border-color, #a0a0a0);
  background-color: rgba(21, 26, 48, 0.5);
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 8px;
  text-align: center;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4);
  }
  .item-name {
    font-size: 14px;
  }
  .item-quantity {
    font-size: 12px;
    color: #a0a0a0;
    margin-top: 5px;
  }
}

.empty-grid {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: #a0a0a0;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(5px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  position: relative;
  background-color: transparent;
  border: none;
  width: 90%;
  max-width: 380px;

  .close-button {
    position: absolute;
    top: 5px;
    right: 5px;
    background: transparent;
    border: none;
    color: rgba(255, 255, 255, 0.5);
    font-size: 28px;
    font-weight: bold;
    line-height: 1;
    cursor: pointer;
    padding: 5px;
    transition: color 0.3s ease;

    &:hover {
      color: rgba(255, 255, 255, 0.9);
    }
  }
}
