
@use '../../styles/theme/variables' as *;
@use '../../styles/theme/mixins' as *;

.save-load-panel {
  background-color: transparent;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: $spacing-lg;
  height: 100%;
  border: none;
  border-radius: 0;
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba($color-gold-liu, 0.3);
  padding-bottom: $spacing-md;
}

.panel-title {
  color: $color-gold-pale;
  font-family: $font-family-title;
  font-size: $font-size-h2;
}

.header-actions {
  display: flex;
  gap: $spacing-md;
}

.header-btn {
  @include button-primary;
  padding: $spacing-sm $spacing-lg;
}

.save-slots-container {
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: $spacing-xl;
}

.slot-group {
  display: flex;
  flex-direction: column;
  gap: $spacing-md;
}

.group-title {
  color: $color-white-moon;
  font-family: $font-family-main;
  font-size: $font-size-large;
  font-weight: bold;
  border-bottom: 1px solid rgba($color-gold-liu, 0.2);
  padding-bottom: $spacing-sm;
  margin-bottom: $spacing-sm;
}

.save-slot {
  @include frosted-glass(rgba($color-charcoal-glass, 0.6), 10px);
  border: 1px solid rgba($color-gold-liu, 0.2);
  border-radius: $border-radius-sm;
  padding: $spacing-md;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.2s ease-in-out;

  &:hover {
    background-color: rgba($color-gold-liu, 0.05);
    border-color: rgba($color-gold-liu, 0.4);
  }
}

.slot-info {
  display: flex;
  flex-direction: column;
  gap: $spacing-xs;
}

.slot-name {
  color: $color-white-moon;
  font-weight: bold;
}

.slot-timestamp {
  color: $color-grey-stone;
  font-size: $font-size-small;
}

.slot-diary {
  margin-top: $spacing-xs;
  padding-top: $spacing-xs;
  border-top: 1px solid rgba($color-gold-liu, 0.1);
  font-size: $font-size-small;
  color: $color-white-moon;

  .log-date {
    font-style: italic;
    color: $color-grey-stone;
  }

  .log-title {
    display: block;
    margin-top: 2px;
    color: $color-white-moon;
    font-weight: 500;
  }
}

.slot-actions {
  display: flex;
  gap: $spacing-sm;
}

.action-btn {
  @include button-secondary;
  padding: $spacing-xs $spacing-md;
  font-size: $font-size-small;

  &.delete-btn {
    border-color: rgba($color-red-chi, 0.6);
    color: rgba($color-red-chi, 0.8);

    &:hover:not(:disabled) {
      background-color: rgba($color-red-chi, 0.15);
      border-color: $color-red-chi;
      color: $color-red-chi;
    }
  }

  &:disabled {
    opacity: 0.4;
    cursor: not-allowed;
    border-color: $color-grey-stone;
    color: $color-grey-stone;
  }
}

.auto-save-toggle {
  margin-top: $spacing-md;
  color: $color-white-moon;

  label {
    display: flex;
    align-items: center;
    gap: $spacing-sm;
    cursor: pointer;
  }
}
