
@use '../../styles/theme/variables' as *;

li {
  display: contents;
}

.object-key {
  color: $color-gold-pale;
  font-weight: bold;
  cursor: help;
  text-align: right;
  white-space: nowrap;
  padding-bottom: $spacing-lg;
}

.value-cell {
  padding-bottom: $spacing-lg;
  min-width: 0;

  > ul {
    list-style-type: none;
    padding-left: $spacing-md;
    margin-top: $spacing-sm;
    border-left: 1px solid rgba($color-gold-liu, 0.2);
    display: grid;
    grid-template-columns: auto 1fr;
    gap: $spacing-sm $spacing-md;

    &.multi-column {
      grid-template-columns: repeat(2, auto 1fr);
      gap: $spacing-sm $spacing-lg;
    }
  }
}

.object-value {
  color: $color-white-moon;
}

.progress-bar-container {
  width: 100%;
  background-color: rgba($color-black-void, 0.5);
  border-radius: $border-radius-sm;
  position: relative;
  height: 24px;
  border: 1px solid rgba($color-gold-liu, 0.3);
}

.progress-bar {
  background: linear-gradient(90deg, rgba($color-cyan-tian, 0.5) 0%, rgba($color-cyan-tian, 0.8) 100%);
  height: 100%;
  border-radius: $border-radius-sm;
  transition: width 0.3s ease;
}

.progress-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: $color-white-moon;
  font-size: $font-size-small;
  text-shadow: 1px 1px 2px $color-black-void;
}

.data-item {
  display: flex;
  flex-direction: column;
  gap: $spacing-xs;
}

.data-item-row {
  display: grid;
  grid-template-columns: 80px 1fr;
}

.data-item-key {
  color: $color-gold-liu;
  font-weight: normal;
  text-align: right;
  padding-right: $spacing-sm;
}

.data-item-value {
  color: $color-white-moon;
}
