
@use '../../styles/theme/variables' as *;
@use '../../styles/theme/mixins' as *;

.database-item-card {
  padding: $spacing-md;
  min-width: 350px;

  .info-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: $spacing-lg;
  }

  .info-item {
    display: flex;
    flex-direction: column;
    gap: $spacing-xs;

    strong {
      font-family: $font-family-main;
      color: $color-gold-pale;
      font-size: 12px;
      text-transform: uppercase;
      letter-spacing: 1px;
      opacity: 0.7;
    }

    span {
      font-size: 14px;
      color: $color-white-moon;
      line-height: 1.6;
      white-space: pre-wrap;
    }

    &.full-width {
      grid-column: 1 / -1;
    }
  }
}
