// [Theme] Reincarnation Simulator - Visual Recast
// Phase 1: Style Foundation
// File: _variables.scss
// Desc: Defines the core design tokens for the "Guixu Dao Mirror" theme.

// ==========================================================================
// 1. Color System: 
// ==========================================================================

// 1.1. Base Palette
$color-black-void: #0a0f1e; //  (Main Background)
$color-indigo-deep: #16212c; //  (Alternative BG)
$color-charcoal-glass: rgba(22, 33, 44, 0.85); //  (Main Panel BG)

// 1.2. Accent Palette
$color-gold-liu: #d4af37; //  (Primary Accent, Authority)
$color-cyan-tian: #3a8f9d; //  (Secondary Accent, Spirit)
$color-red-chi: #c74343; //  (Warning, Danger)
$color-purple-mystery: #8a5db5; //  (Mystery, Rare)

// 1.3. Text Palette
$color-white-moon: #e0e0e0; //  (Primary Text)
$color-gold-pale: #f0e6d2; //  (Important Text)
$color-grey-stone: #a0a0a0; //  (Secondary/Disabled Text)

// ==========================================================================
// 2. Typography: 
// ==========================================================================

// Note: Specific font-family values will be set in _base.scss
// These are semantic variables for consistency.
$font-family-main: 'Siyuan Song', 'Source Han Serif SC', serif; //  (/)
$font-family-title: 'ZCOOL KuaiLe', '', cursive; // / ()

$font-size-base: 14px;
$font-size-small: 12px;
$font-size-large: 16px;
$font-size-h1: 24px;
$font-size-h2: 20px;
$font-size-h3: 18px;

$line-height-base: 1.7;

// ==========================================================================
// 3. Layout & Spacing
// ==========================================================================

$spacing-unit: 4px;
$spacing-xs: $spacing-unit; // 4px
$spacing-sm: $spacing-unit * 2; // 8px
$spacing-md: $spacing-unit * 4; // 16px
$spacing-lg: $spacing-unit * 6; // 24px
$spacing-xl: $spacing-unit * 8; // 32px

$border-radius-sm: 3px;
$border-radius-md: 5px;

// ==========================================================================
// 4. Panel & Component Specific
// ==========================================================================

$panel-border-width: 2px;
$panel-title-height: 40px;
$top-status-bar-height: 48px;
$side-panel-width: 60px;
