
.npc-list-panel {
  padding: 15px;
}

.panel-section {
  margin-bottom: 20px;
  padding: 15px;
  background: white;
  border-radius: 8px;
  border: 1px solid #eae6dd;
}

.section-title {
  font-size: 14px;
  font-weight: 600;
  color: #3a3f4b;
  margin-bottom: 12px;
}

.npc-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.npc-item {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background-color: #f8f9fa;
  border-radius: 6px;
}

.npc-name {
  font-weight: 500;
}

.npc-relationship {
  color: #6c757d;
}
