
@use '../../../styles/theme/variables' as *;
@use '../../../styles/theme/mixins' as *;

.grid-layout {
  display: grid;
  grid-template-columns: 1fr;
  gap: $spacing-lg;
}

.personality-kernel {
  .kernel-content {
    display: flex;
    flex-direction: column;
    gap: $spacing-lg;
  }
  .kernel-section {
    .kernel-title {
      font-family: $font-family-title;
      font-size: $font-size-base;
      color: $color-gold-pale;
      margin-bottom: $spacing-sm;
      border-bottom: 1px solid rgba($color-gold-liu, 0.2);
      padding-bottom: $spacing-sm;
    }
    .kernel-text {
      white-space: pre-wrap;
      &.identifier {
        font-style: italic;
        color: $color-cyan-tian;
      }
    }
  }
}

.profile-list {
  list-style: none;
  padding-left: $spacing-md;
  .profile-item {
    margin-bottom: $spacing-md;
    border-left: 3px solid rgba($color-gold-liu, 0.3);
    padding-left: $spacing-md;
    .profile-context {
      font-weight: bold;
      color: $color-white-moon;
    }
    .profile-behavior,
    .profile-motivation {
      font-size: $font-size-small;
      color: $color-grey-stone;
      span {
        color: $color-gold-pale;
        font-weight: bold;
      }
    }
  }
}

.dialogue-style {
  p {
    margin-bottom: $spacing-sm;
  }
  .phrase-tag {
    background-color: rgba($color-cyan-tian, 0.15);
    color: $color-cyan-tian;
    cursor: default;
  }
}

.detail-item {
  &.full-width {
    grid-column: 1 / -1;
  }
  .detail-key {
    color: $color-gold-pale;
    font-family: $font-family-title;
    font-size: $font-size-large;
    display: block;
    margin-bottom: $spacing-md;
  }
}

.placeholder {
  color: $color-grey-stone;
  font-style: italic;
}

.tags-container {
  display: flex;
  flex-wrap: wrap;
  gap: $spacing-sm;
}

.tag {
  display: inline-block;
  background-color: rgba($color-cyan-tian, 0.15);
  color: $color-cyan-tian;
  padding: 4px 8px;
  border-radius: $border-radius-sm;
}

.talent-tag {
  background-color: rgba($color-gold-liu, 0.2);
  color: $color-gold-pale;
  cursor: help;
}

.skills-list {
  display: flex;
  flex-direction: column;
  gap: $spacing-sm;
}

.skill-item {
  background: rgba($color-black-void, 0.4);
  border-radius: $border-radius-sm;
  padding: $spacing-sm $spacing-md;
  cursor: pointer;
  transition: background-color 0.2s ease;

  &:hover {
    background: rgba($color-cyan-tian, 0.1);
  }
}

.skill-header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  h3 {
    margin: 0;
    font-size: $font-size-base;
    color: $color-white-moon;
  }
}

.skill-level {
  font-size: $font-size-small;
  color: $color-grey-stone;
}
.personality-kernel {
  .kernel-content {
    display: flex;
    flex-direction: column;
    gap: $spacing-lg;
  }
  .kernel-section {
    .kernel-title {
      font-family: $font-family-title;
      font-size: $font-size-base;
      color: $color-gold-pale;
      margin-bottom: $spacing-sm;
      border-bottom: 1px solid rgba($color-gold-liu, 0.2);
      padding-bottom: $spacing-sm;
    }
    .kernel-text {
      white-space: pre-wrap;
      &.identifier {
        font-style: italic;
        color: $color-cyan-tian;
      }
    }
  }
}

.profile-list {
  list-style: none;
  padding-left: $spacing-md;
  .profile-item {
    margin-bottom: $spacing-md;
    border-left: 3px solid rgba($color-gold-liu, 0.3);
    padding-left: $spacing-md;
    .profile-context {
      font-weight: bold;
      color: $color-white-moon;
    }
    .profile-behavior,
    .profile-motivation {
      font-size: $font-size-small;
      color: $color-grey-stone;
      span {
        color: $color-gold-pale;
        font-weight: bold;
      }
    }
  }
  .profile-details {
    padding-top: $spacing-sm;
  }
  .toggle-icon {
    display: inline-block;
    transition: transform 0.2s ease-in-out;
    font-size: 0.8em;
    margin-left: $spacing-sm;
    &.is-open {
      transform: rotate(90deg);
    }
  }
}

.dialogue-style {
  p {
    margin-bottom: $spacing-sm;
  }
  .phrase-tag {
    background-color: rgba($color-indigo-deep, 0.3);
    color: $color-white-moon;
    cursor: default;
  }
}
