
.item-detail-panel {
  background-color: rgba(21, 26, 48, 0.6);
  border-radius: 12px;
  padding: 20px;
  color: #e0e0e0;
  height: 100%;
  overflow-y: auto;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.item-details {
  .item-name {
    font-family: 'Noto Serif SC', serif;
    font-size: 22px;
    margin: 0 0 10px 0;
  }
  .item-tags {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
    .tag {
      padding: 4px 10px;
      border-radius: 20px;
      font-size: 12px;
      font-weight: bold;
    }
  }
  .item-description {
    font-size: 14px;
    line-height: 1.6;
    color: #c0c0c0;
    margin-bottom: 20px;
    white-space: pre-wrap;
  }
  .item-effects {
    margin-top: 20px;
    padding-top: 15px;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    h4 {
      font-family: 'Noto Serif SC', serif;
      font-size: 16px;
      color: #fafad2;
      margin: 0 0 8px 0;
    }
    p {
      font-size: 14px;
      color: #c0c0c0;
      margin: 0;
      white-space: pre-wrap;
    }
  }
}

.placeholder {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: #a0a0a0;
  font-style: italic;
}
