
@use '../../styles/theme/variables' as *;
@use '../../styles/theme/mixins' as *;

.diary-app-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: #1a1d2e;
  color: #c8c8d4;
  overflow: hidden;
  position: relative;
}

.app-content {
  flex-grow: 1;
  overflow-y: auto;
  padding: 20px;
}

.side-nav-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 100;
}

.side-nav {
  position: absolute;
  top: 0;
  left: 0;
  width: 250px;
  height: 100%;
  background-color: #141726;
  padding: 20px;
  z-index: 101;
  transform: translateX(0);
  box-shadow: 5px 0 15px rgba(0, 0, 0, 0.3);

  h3 {
    color: #d4af37;
  }
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  li {
    padding: 10px;
    cursor: pointer;
    border-radius: 4px;
    margin-bottom: 5px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    &.active {
      background-color: #d4af37;
      color: #141726;
    }
  }
}

.slide-enter-active,
.slide-leave-active,
.slide-fade-enter-active,
.slide-fade-leave-active {
  transition: all 0.3s ease;
}

.slide-enter-from,
.slide-leave-to {
  transform: translateX(-100%);
}
.slide-fade-enter-from,
.slide-fade-leave-to {
  opacity: 0;
}

.diary-header {
  text-align: center;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  .date-weather {
    font-size: 14px;
    color: #8a8a9e;
    margin-bottom: 10px;
  }
  .title {
    font-size: 24px;
    color: #e0e0e0;
    margin: 0;
    font-weight: 600;
  }
}

.diary-section {
  margin-bottom: 15px;
  &.collapsible h2 {
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    user-select: none;
    .arrow {
      transition: transform 0.3s ease;
      display: inline-block;
      &.open {
        transform: rotate(90deg);
      }
    }
  }
  h2 {
    font-size: 18px;
    color: #d4af37;
    border-bottom: 1px solid #d4af37;
    padding-bottom: 8px;
    margin-top: 0;
    margin-bottom: 15px;
  }
  p,
  ul {
    font-size: 15px;
    line-height: 1.8;
    white-space: pre-wrap;
  }
  ul {
    padding-left: 20px;
  }
  li {
    margin-bottom: 8px;
  }
}

.collapsible-content {
  padding-top: 10px;
}

.event-item,
.relation-item {
  background-color: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 15px;
  border-left: 3px solid #3a8f9d;
  p {
    margin: 5px 0;
  }
  strong {
    color: #a9a9c8;
  }
  .t {
    font-style: italic;
    color: #8a8a9e;
  }
}

.relation-item {
  border-left-color: #8a5db5;
}
.empty-state {
  text-align: center;
  color: #888;
  padding-top: 5em;
  font-style: italic;
}
