
@use '../../styles/theme/variables' as *;

.attribute-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: $spacing-md;
  width: 100%;
}

.attribute-item {
  display: flex;
  flex-direction: column;
  background: rgba($color-indigo-deep, 0.5);
  border-radius: $border-radius-md;
  padding: $spacing-md;
  border: 1px solid rgba($color-gold-liu, 0.2);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: -80%;
    width: 60%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba($color-gold-pale, 0.08), transparent);
    transform: skewX(-25deg);
    transition: left 0.6s ease;
  }

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba($color-gold-pale, 0.08);
    border-color: rgba($color-gold-pale, 0.3);
    &::before {
      left: 120%;
    }
  }
}

.attribute-key {
  font-size: $font-size-base;
  color: $color-grey-stone;
  margin-bottom: $spacing-sm;
  font-family: $font-family-main;
}

.attribute-value {
  font-size: $font-size-h3;
  font-family: $font-family-title;
  color: $color-gold-pale;
  font-weight: 400;
  text-shadow: 0 0 8px rgba($color-gold-liu, 0.5);
  margin-bottom: $spacing-sm;
}

.progress-bar-container {
  width: 100%;
  height: 18px;
  background-color: rgba($color-black-void, 0.5);
  border-radius: $border-radius-sm;
  border: 1px solid rgba($color-gold-liu, 0.1);
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.progress-bar {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background: linear-gradient(90deg, $color-cyan-tian, darken($color-cyan-tian, 15%));
  border-radius: $border-radius-sm;
  transition: width 0.5s ease-in-out;
}

.progress-text {
  position: relative;
  z-index: 1;
  font-size: 10px;
  color: $color-white-moon;
  text-shadow: 0 0 2px $color-black-void;
}
